@import url(https://fonts.googleapis.com/css?family=Raleway:600,800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway:400,800&display=swap);
@charset "UTF-8";

* {
    margin:  0;
    border:  0;
    padding: 0;
    outline: 0;

    list-style: none;
    text-decoration: none;
    text-rendering: optimizeLegibility;

    box-sizing: border-box;

    -webkit-webkit-font-smoothing: subpixel-antialised;
       -moz-webkit-font-smoothing: subpixel-antialised;
        -ms-webkit-font-smoothing: subpixel-antialised;
         -o-webkit-font-smoothing: subpixel-antialised;
}

body {
    height: 100vh;
}

:root {
    --accent: #000;
}

#background {
    position: absolute;
    background: white;
    height: 100%;
    width: 100%;
    background: linear-gradient(to top, #6c8dbd -100%, #fff 60%);
}

#card {
    z-index: 1;
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    width: 300px;
    height: 300px;
    border-radius: 12%;
    box-shadow: 0 20px 50px rgba(0, 0, 0, .2);
    background: linear-gradient(to top, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.45));
    transition: all .5s ease-in-out;
    -webkit-animation: bounce-in .5s cubic-bezier(0.45, 0.05, 0.55, 0.95);
            animation: bounce-in .5s cubic-bezier(0.45, 0.05, 0.55, 0.95);
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}

@-webkit-keyframes bounce-in {
    from {
        transform: scale(0);
    }
    to {
        transform: scale(1);
    }
}

@keyframes bounce-in {
    from {
        transform: scale(0);
    }
    to {
        transform: scale(1);
    }
}

#card.fullscreen {
    width: 100%;
    height: 100%;
}

#card .info {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Raleway', sans-serif;
    margin-top: 3em;
    transition: all .3s ease-in-out;
}

#card .info .fullname {
    font-size: 12pt;
    font-weight: 800;
    color: #51576b;
}

#card .info .surname {
    font-weight: 600;
}

#card .info .role {
    font-size: 9pt;
    font-weight: 500;
    line-height: 3;
    color: #7c839a;
}

#card .more {
    border-radius: 30%;
}

#card .more {
    position: absolute;
    bottom: -20px;
    width: 40px;
    height: 40px;
    cursor: pointer;
    background-color: #47485a;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 10px 20px rgba(0, 0, 0, .3);
    transition: background-color .3s, all .6s;
    -webkit-animation: bounce 3s infinite ease-in-out;
            animation: bounce 3s infinite ease-in-out;
}

#card .more:not(.expanded):hover {
    background-color: #575871;
}

#card .more.expanded {
    -webkit-animation: none;
            animation: none;
    background-image: none;
    cursor: default;
    width: 100%;
    bottom: 0;
    border-radius: 30px;
    box-shadow: none;
    overflow: hidden;
    -webkit-animation: expand-info .5s forwards linear;
            animation: expand-info .5s forwards linear;
    -webkit-animation-delay: .5s;
            animation-delay: .5s;
}

#card.fullscreen .more.expanded {
	border-radius: 0;
	background: black;
}

#card .more.expanded .dots {
    position: absolute;
    width: 100%;
    height: 100%;
}

#card .more.expanded .more-info {
    z-index: 1;
}

#card .more-info {
    width: 100%;
    height: 100%;
	transition: background .3s ease-in;
}

#card.fullscreen .more.expanded .more-info {
	background: #19181b;
}

#card .paginator {
    width: 100%;
    height: 100%;
    display: inline-grid;
    align-items: center;
    grid-template-rows: [back] 80px [content] auto [menu] 80px;
}

#card .paginator .content {
    padding: 0 1.8em 2em 1.8em;
    height: 100%;
}

#card .paginator .go-back {
    background: url(/homepage/static/media/back.efcdae14.svg) center no-repeat;
    width: 9px;
    height: 14px;
    grid-row: back;
    cursor: pointer;
    padding: 1em;
    margin-left: 20px;
    -webkit-filter: opacity(.45);
            filter: opacity(.45);
    transition: -webkit-filter .2s ease-in-out;
    transition: filter .2s ease-in-out;
    transition: filter .2s ease-in-out, -webkit-filter .2s ease-in-out;
}

#card.fullscreen .go-back {
	--shadow-spacing: 8px;
	-webkit-filter: drop-shadow(var(--shadow-spacing) 0 0 #bf4567);
	        filter: drop-shadow(var(--shadow-spacing) 0 0 #bf4567);
}

#card.fullscreen .go-back:hover {
	--shadow-spacing: 0;
}

#card:not(.fullscreen) .paginator .go-back:hover {
    -webkit-filter: opacity(1);
            filter: opacity(1);
}

#card .more-info .content {
    grid-row: content;
}

#card .more-info #menu {
    grid-row: menu;
}

#card .more.expanded .dots div {
    --position: -300px;
    --delay: .6s;
    margin: 0;
    bottom: var(--position);
    background: rgba(36, 39, 56, 0.5);
    -webkit-animation: transition-expand-more var(--delay) forwards linear;
            animation: transition-expand-more var(--delay) forwards linear;
    -webkit-animation-delay: var(--delay);
            animation-delay: var(--delay);
    border-radius: 0;
    width: 100%;
    height: 100%;
    position: absolute;
}

#card .more.expanded .dots div:nth-child(1) {
    background: #af453b;
}

#card .more.expanded .dots div:nth-child(2) {
    --delay: .7s;
    background: #47a579;
}

#card .more.expanded .dots div:nth-child(3) {
    --delay: .8s;
    background: #3b3c42;
}

#card .more.retract {
    -webkit-animation: retract .5s forwards linear;
            animation: retract .5s forwards linear;
}

#card .more .dots.hidden {
    visiblity: hidden;
    display: none;
}

#card .more .dots {
    display: flex;
    align-items: center;
    justify-content: center;
}

#card .more .dots div {
    width: 4px;
    height: 4px;
    background: #fff;
    margin: 2px;
    border-radius: 50%;
    transition: all .3s;
}

#card .more:not(.expanded) .more-info {
    visibility: hidden;
    display: none;
}

#card .more:not(.expanded):hover .dots div {
    -webkit-animation: bounce-dots 1s infinite linear;
            animation: bounce-dots 1s infinite linear;
}

#card .more:not(.expanded) .dots div:nth-child(1) {
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}

#card .more:not(.expanded) .dots div:nth-child(2) {
    -webkit-animation-delay: .05s;
            animation-delay: .05s;
}

#card .more:not(.expanded) .dots div:nth-child(3) {
    -webkit-animation-delay: .08s;
            animation-delay: .08s;
}

@-webkit-keyframes bounce {
    50% {
        bottom: -10px;
    }
}

@keyframes bounce {
    50% {
        bottom: -10px;
    }
}

@-webkit-keyframes bounce-dots {
    45% {
        margin-top: 10px;
    }
    75% {
        margin-top: -10px;
    }
}

@keyframes bounce-dots {
    45% {
        margin-top: 10px;
    }
    75% {
        margin-top: -10px;
    }
}

@-webkit-keyframes expand-info {
    from {
        height: 40px;
    }
    to {
        height: 100%;
    }
}

@keyframes expand-info {
    from {
        height: 40px;
    }
    to {
        height: 100%;
    }
}

@-webkit-keyframes retract {
    from {
        height: 100%;
    }
    to {
        height: 40px;
    }
}

@keyframes retract {
    from {
        height: 100%;
    }
    to {
        height: 40px;
    }
}

@-webkit-keyframes transition-expand-more {
    from {
        bottom: -300px;
    }
    to {
        bottom: 0;
    }
}

@keyframes transition-expand-more {
    from {
        bottom: -300px;
    }
    to {
        bottom: 0;
    }
}

#menu {
    position: relative;
    width: 100%;
    height: 80px;
	display: flex;
	justify-content: center;
	align-items: flex-end;
	overflow: hidden;
}

#menu .sections {
	position: relative;
    display: inline-grid;
    background: rgba(255, 255, 255, .12);
    grid-template-columns: repeat(4, .5fr);
    width: 100%;
    height: 60px;
    padding-left: .8em;
    border-radius: 30px;
	transition: width .5s ease-in-out;
}

#card.fullscreen #menu .sections {
	width: 400px;
}

#card.fullscreen .section.fixed {
	border-radius: 30px;
}

#menu .section {
    cursor: pointer;
    border-radius: 20px;
    transition: -webkit-filter .1s ease-in-out;
    transition: filter .1s ease-in-out;
    transition: filter .1s ease-in-out, -webkit-filter .1s ease-in-out;
}

#menu .section:not(.fixed) {
    -webkit-filter: opacity(.3);
            filter: opacity(.3);
}

#menu .mark {
    --blur: 12px;
    --spread: 2px;
    position: absolute;
    width: 8px;
    height: 5px;
    background: #fff;
    border-radius: 50%;
    left: 45px;
    bottom: -2px;
    box-shadow: 0 -2px var(--blur) var(--spread) #fff;
    transition: left .2s ease-in-out;
}

#menu .section.active ~ .mark {
    -webkit-animation: shine .3s forwards linear;
            animation: shine .3s forwards linear;
}

#menu .section:hover {
    -webkit-filter: opacity(.7);
            filter: opacity(.7);
}

#menu .section.active {
    -webkit-filter: opacity(1);
            filter: opacity(1);
}

#menu .section.profile  { background: url(/homepage/static/media/profile.59340793.svg) center no-repeat; }
#menu .section.projects { background: url(/homepage/static/media/projects.5765fec2.svg) center no-repeat; }
#menu .section.fixed    { background: url(/homepage/static/media/github.49666ea6.svg) center no-repeat; }
/* #menu .section.dotfiles { background: var(--asset-dotfiles) center no-repeat; } */

#menu .section.projects.active ~ .mark {
    left: 38.5%;
}

#menu .section.fixed {
    background-color: #bf4567;
    border-radius: 60px;
}

#menu .section::after {
    content: attr(name);
    text-transform: uppercase;
    color: #838487;
    font: 600 9.9px 'Roboto', sans-serif;
    letter-spacing: 1.5px;
    text-align: center;
    display: block;
    position: relative;
    padding: .5em;
    top: 0;
    opacity: 0;
    transition: all .25s ease-in;
    border-radius: 40px;
}

#menu .section:hover::after {
    top: -25px;
    opacity: 1;
}

@-webkit-keyframes shine {
    0% {
        --spread: 0;
    }
    50% {
        --spread: 15px;
        --blur: 30px;
    }
    100% {
        --spread: 2px;
        --blur: 12px;
    }
}

@keyframes shine {
    0% {
        --spread: 0;
    }
    50% {
        --spread: 15px;
        --blur: 30px;
    }
    100% {
        --spread: 2px;
        --blur: 12px;
    }
}

#card.fullscreen #menu {
	margin-bottom: 5%;
}

#profile {
    width: 100%;
    height: 100%;
    -webkit-animation: slide-in .5s forwards linear;
            animation: slide-in .5s forwards linear;
    transition: transform .5s ease-in-out;
    font-family: 'Raleway', sans-serif;
}

#profile h1 {
    font-size: 18pt;
    font-weight: 800;
    color: #fff;
    margin-bottom: .4em;
}

#profile p {
    font-size: 11pt;
    color: #c0c3d8;
}

@-webkit-keyframes slide-in {
    from {
        transform: translateY(100%);
    }
    to {
        transform: translateY(0);
    }
}

@keyframes slide-in {
    from {
        transform: translateY(100%);
    }
    to {
        transform: translateY(0);
    }
}

.timeline {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
}

.projects-list {
	width: 100%;
	height: 100%;
	max-width: 600px;
	position: relative;
}

.projects-list::after {
    content: '';
    display: block;
    background: rgba(255, 255, 255, 0.1);
    width: 1px;
    height: 100%;
	position: absolute;
	top: 0;
}

.project {
    /* color: #fff; */
    /* font-family: 'Robot', sans-serif; */
    /* box-shadow: inset 2px 0 0 red; */
    /* padding-left: 1em; */
    /* display: grid; */
    /* grid-template-rows: 1fr 30px; */
}

#card .avatar {
    position: relative;
    max-width: 100px;
    max-height: 100px;
    overflow: hidden;
    box-shadow: 0 15px 30px rgba(0, 0, 0, .15);
    border-radius: 30%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fff;
    transition: all .3s ease-in-out;
    cursor: pointer;
}

#card .avatar img {
    width: 100%;
}

#card .avatar:hover {
    max-width: 150px;
    max-height: 150px;
}

#card .avatar:hover ~ .info {
    margin-top: 1em;
}

#card .avatar:not(:hover) .followers {
    visibility: hidden;
    bottom: -40px;
}

#card .avatar .followers {
    position: absolute;
    bottom: 0;
    background: rgb(71, 72, 90);
    min-width: 100px;
    width: 80%;
    text-align: center;
    border-radius: 50px;
    padding: .5em;
    color: #fff;
    font: 13px 'Roboto', sans-serif;
    vertical-align: middle;
    transition: all .3s ease-in-out;
}

#card .avatar .followers span {
    font-weight: 800;
}

.loader:not(.loaded) {
    display: none;
}

.loader {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    position: absolute;
    background: #fff;
    z-index: 1;
}

.loader::before,
.loader::after {
    content: '';
    position: absolute;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: transparent;
}

.loader::before {
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
    border: 4px solid black;
    border-bottom: 0;
    border-right: 0;
    border-left: 0;
    -webkit-animation: rotate .5s infinite linear;
            animation: rotate .5s infinite linear;
}

.loader::after {
    box-shadow: 0 0 0 2px rgba(0, 0, 0, .1),
                inset 0 0 0 2px rgba(0, 0, 0, .1);
}

@-webkit-keyframes rotate {
    to {
        transform: rotate(360deg);
    }
}

@keyframes rotate {
    to {
        transform: rotate(360deg);
    }
}

